    /* margin-bottom: 50px; */
    display: flex;
    overflow-x: scroll;
    justify-content: space-evenly;
}
.categories_DOM .categories_box{
    min-width: 250px;
    width: 250px;
    margin: 20px;
    margin-top: 30px;
    margin-bottom: 150px;
    display: grid;
    place-items: center;
    height: 300px;
    position: relative;
    border-radius: 14px;
    box-shadow: 4px 4px 8px rgba(151, 167, 195, 0.5), -4px 0px 8px #FFFFFF;
}
.categories_DOM .categories_box::after{
    content: "";
    position: absolute;
    bottom: -5%;
    width: 90%;
    height: 18px;
    border-radius: 5px;
    box-shadow: 4px 4px 8px rgba(151, 167, 195, 0.5), -4px 0px 8px #FFFFFF;
}
.categories_DOM .categories_box::before{
    content: "";
    position: absolute;
    bottom: -10.5%;
    width: 80%;
    height: 18px;
    border-radius: 5px;
    box-shadow: 4px 4px 8px rgba(151, 167, 195, 0.5), -4px 0px 8px #FFFFFF;
}
.categories_box h4{
    color: #193566;
    font-size: 16px;
    text-transform: uppercase;
}
.categories_box img{
    width: 80%;
    height: 60% !important;
    border-radius: 12px;
}
.categories_box .navButton{
    background: #ECF0F3;
    -webkit-transform: translateY(200%);
            transform: translateY(200%);
    cursor: pointer;
    box-shadow: 4px 4px 8px rgba(151, 167, 195, 0.5), -4px -4px 8px #FFFFFF;
    border-radius: 20px;
    background: #193566;
    padding: 10px 30px;
    margin-bottom: 40px;
}
.categories_box .navButton button{
    border: none;
