    top: 14.17%;
    bottom: 50.56%;
}
.homevectorshape{
    position: absolute;
    left: 10.87%;
    right: 11.11%;
    top: 14.17%;
    bottom: 50.56%;
    background: #ECF0F3;
    mix-blend-mode: normal;
    border: 13px solid #ECF0F3;
    box-shadow: -4px -4px 20px rgba(255, 255, 255, 0.25), 4px 4px 20px rgba(124, 174, 211, 0.25), inset 15px 15px 35px rgba(115, 112, 112, 0.25), inset -15px -15px 35px #FFFFFF;
}
#maincarousel .carousel-inner{
    overflow-x: hidden !important;
    overflow-y: scroll !important;
    height: calc(100vh - 64px) !important;
}
#maincarousel>.carousel-indicators{
    position: fixed !important;
}
#maincarousel .carousel-item{
    height: calc(100vh-64px) !important;
}
#maincarousel>.carousel-indicators:first-child [data-bs-target] {
    border-radius: 50% !important;
    background: #193566;
    height: 10px !important;
    width: 10px !important;
    margin-bottom: 10px;
}
#maincarousel .carousel-indicators [data-bs-target] {
    border-top: 1px solid transparent !important;
    border-bottom: 1px solid transparent !important;
}
a{
    text-decoration: none !important;
}
