.blockLine{
    font-family:'Montserrat';
    display: block;
    font-weight: normal;
    position: relative;
    padding-left: 30px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 12px;
}
.blockLine input{
    visibility: hidden;
    width: 0;
}
.blockLine:hover input ~ .checked{
    background-color: #ccc;
}
.checked{
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    background-color: #ECF0F3;
    box-shadow: inset 2.5px 2.5px 1px 0 rgb(0 0 0 / 18%);
    border-radius: 3px;
}
.blockLine input:checked ~ .checked{
    background-color: #97A7C3;
  }
.blockLine input:checked ~ .checked:after {
    display: block;
  }
.blockLine .checked:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    transform: rotate(45deg);
  }
.checked:after {
    content: "";
    position: absolute;
    display: none;
  }
